import { BrowserRouter, Routes, Route } from "react-router-dom";
import Login from "./pages/Login";
import Header from "./Header";
function App() {
  return (
    <BrowserRouter>
      <Header />
      <Routes>
        <Route path="/"></Route>
        <Route path="/login" element={<Login />} />
      </Routes>
    </BrowserRouter>
  );
}

export default App;
